import React from 'react';

class Prompt extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
            message: ''
        }
    }

    showPrompt (message) {
        this.setState({visible: true});
        this.setState({message: message});

        setTimeout(() => {
            this.setState({visible: false});
        }, 400);
    }

    componentDidMount () {
        React.$prompt = this.showPrompt.bind(this);
    }

    render () {
        return (
            <div className={['plug-prompt', this.state.visible ? 'active' : ''].join(' ')}>
                <div className="content">
                    {this.state.message}
                </div>

                <style jsx>{`
                    .plug-prompt {
                        position: fixed;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        background: #666;
                        padding: 20px 30px;
                        font-size: 14px;
                        color: #fff;
                        border-radius: 6px;
                        opacity: 0;
                        width: 0;
                        height: 0;
                        transition: all 2s ease-out;
                    }
                    .plug-prompt.active {
                        opacity: 1;
                        width: auto;
                        height: auto;
                    }
                `}</style>
            </div>
        )
    }
}
export default Prompt;
